<template><markdown-wrapper><h1 id="affix">Affix <a class="header-anchor" href="#affix">&#128279;</a></h1>
<blockquote>
<p>The affix component toggles <code v-pre>position: fixed</code> on and off, emulating the effect found with <code v-pre>position: sticky</code>.</p>
</blockquote>
<p>The navigation <strong>on the right</strong> is a live demo of the affix component (hidden on mobile devices).</p>
<p><strong>Note</strong>:</p>
<ul>
<li>You must provide CSS for the positioning and width of your affixed content.</li>
<li>Do not use affix on an element contained in a relatively positioned element, such as a pulled or pushed column.</li>
</ul>
